export default function storybookRuntimeOptions(getStorybook) {
  const runtime = 'storybook';
  return {
    runtime,
    renderSpec({ runtime: specRuntime, input }) {
      if (specRuntime !== runtime) {
        throw new Error(`Storybook plugin cannot handle ${specRuntime} specs`);
      }

      const { kind, name } = input;

      const story = getStorybook()
        .find(c => c.kind === kind)
        .stories.find(s => s.name === name);

      return story.render({
        kind,
        name,
      });
    },
    specs() {
      return getStorybook()
        .map(({ kind, stories }) =>
          stories.map(({ name }) => ({
            name,
            componentName: kind,
            runtime,
            input: JSON.stringify({
              kind,
              name,
            }),
          }))
        )
        .reduce((a, b) => [...a, ...b], []); // flatten
    },
  };
}
